<template>
	<div class="quotation">
		<div class="title">
			<span>币种行情</span>
			<!--<mu-button @click="addQuotation" fab color="#B9862F" style="float: right;width: 32px;height: 32px;margin-top: 9px;margin-right: 10px;">
			    <i class="iconfont" style="font-size: 22px;margin-top: 2px;">&#xe6df;</i>
			</mu-button>-->
		</div>
		<mu-container class="market">
			<mu-paper :z-depth="0">
				<mu-load-more @refresh="refresh" :refreshing="refreshing">
					<mu-data-table stripe :hideHeader="true" :height="height" no-data-text="暂未添加行情..." style="margin-bottom: 50px;width: 100%;" ref="container" :min-col-width="colWidth" :data="quotationList">
						<template slot-scope="scope">
							<!--<span style="margin-top: 10px;">{{scope.row.vol_a}}</span>-->
							<td class="is-left">
								<i v-if="scope.row.rate_percent<0" style="color: red;position: relative;right: 5px;" class="iconfont">&#xeb15;</i>
								<i  v-if="scope.row.rate_percent>0" style="color: limegreen;position: relative;right: 5px;" class="iconfont">&#xeb19;</i>
								<span style="position: relative;top: -5px;">{{scope.row.symbol}}
								<span style="font-size: 12px;color:#b5b5b5;">/USDT</span></span>
								<br>
								<span style="margin-left: 25px;position: relative;top: -3px;">￥{{Number(scope.row.rate).toFixed(5)}}</span>
							</td>
							<!--<td class="is-center" @click="toDetails(scope.row.no)">{{Number(scope.row.rate).toFixed(5)}}<br><span>￥{{Number(scope.row.usdt_rmb).toFixed(5)}}</span></td>-->
							<td class="is-right">
								{{Number(scope.row.rate).toFixed(5)}}<br>
								<span :style="carbsBtnColor(scope.row.rate_percent)">{{scope.row.rate_percent}}%</span>
							</td>
						</template>
					</mu-data-table>
				</mu-load-more>
			</mu-paper>
		</mu-container>

		<p v-if="quotationList.length==0" style="text-align: center;">暂无数据...</p>
		<!--<p style="text-align: center;" v-show="quotationList.length>10&&!loading">没有更多数据啦~~</p>-->
	</div>
</template>

<script>
	export default {
		name: 'Index',
		data() {
			return {
				sort: {
			        name: '',
			        order: 'asc'
			      },
				quotationList: [{
					curr_a:"ADA",
					curr_b:"USDT",
					curr_suffix:" USDT",
					lq:"0",
					marketcap:"1,269,390,000",
					name:"Cardano",
					name_cn:"艾达币",
					name_en:"Cardano",
					no:19,
					pair:"ada_usdt",
					rate:"0.0408",
					rate_percent:"-0.49",
					supply:31112500000,
					symbol:"ADA",
					trend:"down",
					usdt_rmb:0.27948,
					vol_a:"13379862.0253055944",
					vol_b:"551,500",
				},{
					curr_a:"ETH",
					curr_b:"USDT",
					curr_suffix:" USDT",
					lq:"0",
					marketcap:"1,269,390,000",
					name:"Cardano",
					name_cn:"艾达币",
					name_en:"Cardano",
					no:19,
					pair:"ada_usdt",
					rate:"1.0408",
					rate_percent:"+0.49",
					supply:31112500000,
					symbol:"ETH",
					trend:"down",
					usdt_rmb:1.0408,
					vol_a:"13379862.0253055944",
					vol_b:"551,500",
				}],
				refreshing: false,
				height: "",
			}
		},
		computed: {
			//计算表格每列的宽度
			colWidth() {
				return(window.screen.width) / 2;
			},
			carbsBtnColor() {
				return function(carbs) {
					if(carbs > 0) {
						return 'color:#38BA54;position:relative;top:3px';
					} else if(carbs == 0) {
						return 'color:#DCBB82;position:relative;top:3px';
					} else if(carbs < 0) {
						return 'color:#FF3C00;position:relative;top:3px';
					}
				}
			}
		},
		mounted() {
			this.height = window.screen.height - 105;
			this.getQuotation();
		},
		methods: {
			//添加行情
			addQuotation(){
				this.$router.push('/addQuotation')
			},
			handleSortChange({
				name,
				order
			}) {
				this.quotationList = this.quotationList.sort((a, b) => order === 'asc' ? a[name] - b[name] : b[name] - a[name]);
			},
			// 跳转详情
			toDetails(id) {},
			//获取币种行情
			getQuotation() {
				let _this = this;
				//https://api.yitaifang.com/index/tokens/
				this.$ajax({  
				    url:'https://api.etherscan.io/api?module=block&action=getblockreward&blockno=2165403&apikey=',  
				    dataType:'json',  
				    jsonp:'callback',  
			     	success:function(result){ 
			        	console.log(result) 
			    	},  
			     timeout:2000  
			 	});
			},
			//下拉刷新
			refresh() {
//				this.refreshing = true;
//				this.$refs.container.scrollTop = 0;
//				setTimeout(() => {
//					this.refreshing = false;
//					this.getQuotation();
//				}, 1000)
			},
//			//加载更多
//			load() {
//				this. = true;
//				this.getQuotation();
//			}
		},
		activated(){
			
		},
		watch:{
			'$route' (to,from){
				if(to.path == '/quotation' && from.path == '/addQuotation'){
					//循环暂存选中的币种 ，退出清空
					let chooseQuotationTypeArr = this.$store.state.detailsBackQuotation;
					console.log(chooseQuotationTypeArr)
				  	if(chooseQuotationTypeArr.length>0){
						this.quotationList=[];
				  		for(let i in this.allQuotation) {
							for(let k in chooseQuotationTypeArr){
								if(this.allQuotation[i].symbol == chooseQuotationTypeArr[k]){
									this.quotationList.push(this.allQuotation[i]);
								}
							}
						}
				  	}else{
				  		this.getQuotation();
				  	}
				}
			}
		}
	}
</script>

<style scoped>
	.quotation{
		background: white;
	}
	.router-view {
		padding-top: 0;
	}
	
	.title {
		width: 100%;
		background: white;
		height: 50px;
		line-height: 50px;
		text-align: center;
		font-size: 18px;
		color: #000;
		border-bottom: 1px solid #f5f5f5;
	}
	.is-left, .is-right{
		width: 50%;
	}
</style>


